@media screen and (max-width: 1920px) {
  html {
    font-size: 62.5%;
    height: 100%;
    min-height: 100vh;
  }
  body {
    font-family: Cookie;
    font-weight: 500;
    font-size: 3rem;
    line-height: 1;
    margin: 0;
    color: rgb(0, 0, 0);
    border-radius: 10%;
  }
  body #root {
    height: 100%;
  }
  body #root .container {
    height: 100%;
    width: 100%;
    margin: 0;
  }
  img {
    max-width: 100%;
  }
